import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFlag } from '@fortawesome/free-solid-svg-icons';
import { rankNum } from './pieceUtils';
import { renderPieceWithRank, renderPieceBack } from './renderUtils';

//  FUNO: Mostrar qual pea est numa clula 
export const getPeca = (
    posicao,
    tabuleiro,
    combateAtivo,
    revealCombate,
    faseJogo,
    modoJogo,
    jogadorAtual,
    mostrarTrocaTurno
) => {
    const peca = tabuleiro[posicao];
    if (!peca) return '';

    // Durante combate, SEMPRE mostrar as peas das duas casas envolvidas
    if (
        combateAtivo &&
        (posicao === revealCombate.origem || posicao === revealCombate.destino)
    ) {
        return renderPieceWithRank(peca.numero);
    }

    // Durante configurao, controle de visibilidade
    if (faseJogo === 'configuracao') {
        // No modo IA, NUNCA mostrar as peas Vermelhas (IA)  sempre "de costas"
        if (modoJogo === 'ia') {
            if (peca.jogador === 'Vermelho') {
                return renderPieceBack('Vermelho');
            }
            // Peas Azuis (humano): mostra quando for o turno dele configurar
            return peca.jogador === jogadorAtual
                ? renderPieceWithRank(peca.numero)
                : renderPieceBack('Azul');
        }

        // Modo local (sem IA)
        if (peca.jogador === jogadorAtual) {
            return renderPieceWithRank(peca.numero);
        } else {
            return renderPieceBack(peca.jogador);
        }
    }

    // Durante popup de troca de turno, esconder todas as peas
    if (mostrarTrocaTurno) {
        return '';
    }

    // Durante transio de configurao para jogo, manter estado anterior
    if (faseJogo === 'jogando' && modoJogo === 'ia' && jogadorAtual === "Vermelho") {
        // Se est mudando da configurao para jogo, esconder peas da IA imediatamente
        if (peca.jogador === "Vermelho") {
            return renderPieceBack("Vermelho");
        }
    }

    // Durante o jogo contra IA, s mostrar peas do jogador humano
    if (modoJogo === 'ia') {
        if (peca.jogador === "Azul") {
            return renderPieceWithRank(peca.numero);
        } else {
            return renderPieceBack("Vermelho");
        }
    } else {
        // Modo local - mostrar peas do jogador atual
        if (peca.jogador === jogadorAtual) {
            return renderPieceWithRank(peca.numero);
        } else {
            return renderPieceBack(peca.jogador);
        }
    }
};