
import React from 'react';

function RulesModal({ mostrarRegras, fecharRegras }) {
    if (!mostrarRegras) return null;

    return (
        <div className="modal-overlay" role="dialog" aria-modal="true" aria-label="Regras do jogo">
            <div className="modal">
                <header className="modal-header">
                    <h3>Regras do Jogo</h3>
                    <button className="close-btn" onClick={fecharRegras} aria-label="Fechar"></button>
                </header>
                <div className="modal-content">

                    <h4 className="rule-title">Formao Inicial</h4>

                    <p> Cada jogador escolhe a formao do seu exrcito, distribuindo as peas como desejar nas 4 linhas de sua cor no tabuleiro.</p>

                    <hr></hr>

                    <h4 className="rule-title">Regras de Movimentao</h4>

                    <p> A maioria das peas se move 1 casa ortogonalmente.</p>
                    <p> O Soldado(2) pode andar quantas casas livres quiser, na horizontal ou na vertical. Ele tambm pode andar e atacar no mesmo turno.</p>
                    <p> Nenhuma pea pode se mover na diagonal, pular sobre outras peas, nem se mover para um espao j ocupado por outra pea.</p>
                    <p> Lagos so intransponveis.</p>
                    <p> Somente uma pea pode se mover em cada turno.</p>
                    <p> Uma pea no pode se mover mais do que 5 vezes, sem parar, entre 2 casas.</p>
                    <p> Bomba e Bandeira no se movem.</p>

                    <hr></hr>

                    <h4 className="rule-title">Regras de Ataque</h4>

                    <p> Peas se revelam no ataque; maior patente vence (Existem excees).</p>
                    <p> A pea com o valor menor  capturada e removida do tabuleiro. Se a pea vencedora foi a atacante, ento ela se move para a posio da pea capturada. Se a pea vencedora foi a atacada, ela fica na posio que est.</p>
                    <p> Quando duas peas de mesmo valor se atacam, as duas so removidas do tabuleiro.</p>
                    <p> O ataque  sempre opcional.</p>

                    <hr></hr>

                    <h4 className="rule-title">Regras Especiais de Ataque</h4>

                    <p> Bomba e Desarmadora(3): Todas as peas que atacam uma bomba, so removidas do tabuleiro (inclusive a bomba atacada), exceto a pea desarmadora, que desarma e captura a bomba.</p>
                    <p> Marechal(10) e Assassino(1): O Marechal ganha de todas as outras peas de valor menor, s pode ser capturado se for atacado pelo Assassino. Caso o Marechal ataque o Assassino primeiro, ele vence.</p>

                    <hr></hr>

                    <h4 className="rule-title">Objetivo do Jogo</h4>

                    <p> Capturar a bandeira inimiga, ou eliminar todas as peas do oponente que se movimentam.</p>

                </div>
                <footer className="modal-footer">
                    <button className="nav-btn" onClick={fecharRegras}>Fechar</button>
                </footer>
            </div>
        </div>
    );
}

export default RulesModal;